@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@400;700&display=swap);
.skill-container {
  display: flex;
  flex-direction: column;
}

.introduce {
  margin: 50px 0 20px;
  font-size: 50px;
}

.skill-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.skill-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body {
  background-color: #e8e8e8;
  font-family: 'Urbanist', sans-serif;
  color: #717171;
}

a:link {
  color: #717171;
  text-decoration: none;
}

a:visited {
  color: #717171;
  text-decoration: none;
}

/* loading page */

.loading {
  display: flex;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-icon {
  text-align: center;
  margin-right: 10px;
}

/* movie */

.movie-container {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 2px 2px 4px rgba(0,0,0,.25);
}

.movie-title {
  color: #717171;
  margin-bottom: 10px;
}

.movie-genres {
  display: flex;
  color: #ababab;
}

.movie-poster {
  transition: ease, 0.5s;
}

.movie-container:hover .movie-poster{
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.movie-summary {
  color: #717171;
}

.movie-detail-view {
  display: inline-block;
  color: #ababab;
  font-size: 17px;
  margin: 7px 0 7px;
}

@media screen and (min-width: 320px) {
  .movie-container {
    display: flex;
    flex-direction: column;
    width: 300px;
    height: auto;
    margin: 10px auto;
    padding: 20px 10px;
    background-color: #fff;
  }

  .movie-poster {
    display: block;
    width: 200px;
    height: auto;
    margin: 0 auto 30px;
    box-shadow: 3px 5px 5px #a7a7a7;
  }

  .video-icon {
    font-size: 40px;
  }

  .loading p{
    font-size: 20px;
  }

  .movie-year,  
  .movie-rating,
  .movie-runtime {
    display: block;
    margin-bottom: 7px;
  }


  .movie-title {
    font-size: 20px;
  }

  .movie-year {
    display: block;
    font-size: 17px;
  }

  .movie-genres {
    flex-wrap: wrap;
    width: 250px;
    font-size: 15px;
  }

  .movie-genre {
    margin: 0 7px 7px 0;
  }

  .movie-summary {
    height: 47.5px;
  }

  @media screen and (min-width: 768px) {
    .movie-container {
      flex-direction: row;
      width: 70%;
      height: 310px;
      margin: 40px auto;
    }

    .movie-description {
      display: flex;
      flex-direction: column;
    }

    .movie-poster {
      width: auto;
      margin: 0 20px 0 0;
    }

    .movie-title {
      font-size: 20px;
    }

    .movie-genres {
      width: 200px;
      margin-bottom: 10px;
    }

    .movie-summary {
      height: 60px;
    }

    .movie-title,
    .movie-summary {
      line-height: 1.3;
      padding-right: 25px;
    }
  }

  @media screen and (min-width: 1024px) {
    #root > div {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
    }
    
    .movie-container {
      width: 500px;
      height: 330px;
      margin: 40px 30px;
    }

    .movie-poster {
      width: auto;
    }

    .movie-summary {
      height: auto;
    }
  }

}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  /* font: inherit; */
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input {
  border: none;
}



@media screen and (min-width:320px) {
