    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    background-color: #fff;
    margin: 70px 50px 20px;
    padding-right: 50px;
    border-radius: 30px;
    box-shadow: -2px -3px 10px rgba(0,0,0,.15);
  }

  .location-poster {
    min-width: 300px;
    margin: -10px 30px 0 0;
    box-shadow: 2px 3px 10px rgba(0,0,0,.15);
  }
  
  .location-description {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .location-title {
    font-size: 40px;
    width: 500px;
    margin: 30px 0 50px
  }

  .location-genres {
    display: flex;
